<template>
	<div class="BindingCommodityCode">
		<header><i class="iconfont icon-xiangzuo1" @click="goPrev"></i><span>绑定商品条码</span></header>
		<div class="container">
			<table cellspacing="0">
				<thead>
					<tr>
						<td>选择</td>
						<td  @click="SortArr('BindingCommodityData','sku','SortCondition1',$event)">商品代码</td>
						<td  @click="SortArr('BindingCommodityData','descr','SortCondition2',$event)">商品名称</td>
						<td  @click="SortArr('BindingCommodityData','susr2','SortCondition3',$event)">单位</td>
						<td  @click="SortArr('BindingCommodityData','qtyremain','SortCondition4',$event)">待收数量</td>
						<td  @click="SortArr('BindingCommodityData','qtyexpected','SortCondition5',$event)">预期数量</td>
						<td  @click="SortArr('BindingCommodityData','qtyreceived','SortCondition6',$event)">已收数量</td>
					</tr>
				</thead>
				<tbody>
					<tr v-for='(dataItem,index) in BindingCommodityData'  :id="index"  :key='index'  @click="selectData(index)">
						<td class="iconfont"  :class="{ 'icon-xuanzhong':index==detail_item,'icon-weixuanzhong':index!=detail_item}"></td>
						<td>{{dataItem.sku}}</td>
						<td>{{dataItem.descr}}</td>
						<td>{{dataItem.susr2}}</td>
						<td>{{dataItem.qtyremain}}</td>
						<td>{{dataItem.qtyexpected}}</td>
						<td>{{dataItem.qtyreceived}}</td>
					</tr>
				</tbody>
			</table>
			<div class="kong"></div>
			<p class="positionFixed"><span class="g_singleBtn" @click="selectData_submit">确定</span></p>
			<div v-if="tipsHide" @touchmove.prevent class="g_tips">
				<div>
					<span>错误信息</span>
					<span>{{msg}}</span>
					<p  @click='tipsHideen(inputFocus)'>确定</p>
				</div>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
	export default{
		name:"BindingCommodityCode",
		data(){
			return{

				BindingCommodityData:[],//列表数据
				detail_item:0,//默认为第一个
				tipsHide:false,//提示框是否出现
				msg:"",//提示信息
				inputFocus:"",
				SortCondition1:false,
				SortCondition2:false,
				SortCondition3:false,
				SortCondition4:false,
				SortCondition5:false,
				SortCondition6:false,

			}
		},
		mounted(){
			$("thead").find("td").eq(0).siblings().append('<i class="iconfont"></i>')
		},
		created(){

			// 按照商品名称排序
			this.BindingCommodityData = this.BubbleSort(this.$store.state.BindingCommodity_data,"descr",true);

		},
		methods:{
			// 返回上一页 扫描商品条码 页面
			goPrev:function(){

				var that = this;

				this.$router.push({
					name:"ScanCommodityCode",
					query:{
						ALtSku:that.$route.query.ALtSku
					}
				})
			},
			//点击选择具体订单
			selectData:function(idx){

				this.detail_item = idx;
				
			},
			// 点击确认按钮 跳转到录入单号页面
			selectData_submit:function(){

				var that = this;

				// 获取最近的一个收货批次
				var LottableUrl = this.HOST + "RFAltSkuBound.do";

				var BindData = this.BindingCommodityData[this.detail_item];

				var Lottable_Data = {
					StorerKey : that.$store.state.Receipt_SubmitData[0].storerkey,//客户代码
					Sku : BindData.sku,//商品代码
					ALtSku : that.$route.query.ALtSku,//商品条码
					AltSkuType : ''   //条码类型  允许为空
				}

				this.httpRequest(LottableUrl,Lottable_Data,function(res){
							
					that.$router.push({
						name:"ScanCommodityCode",
						query:{
							ALtSku:that.$route.query.ALtSku
						}
					})
				})
			}
		}
	}
</script>
<style lang="less" scoped>
	.BindingCommodityCode{
		&>.container{
			width:100%;
			padding-bottom: 130/50rem;
			table{
				tr{
					td{
						min-width: 160/50rem;
					}
					td:nth-child(1),td:nth-child(4){
						min-width: 110/50rem;
					}
				}
			}
			.iconfont{
				font-size: 40/50rem;
			}
		}
	}
	@media screen and (max-width: 332px) and (min-width: 251px){
		.BindingCommodityCode{
			&>.container{
				table{
					tr{
						td{
							min-width: 200/50rem;
						}
						td:nth-child(1),td:nth-child(4){
							min-width: 130/50rem;
						}
					}
				}
				.iconfont{
					font-size: 50/50rem;
				}
			}
		}
	}
	@media screen and (max-width: 250px){
		.BindingCommodityCode{
			&>.container{
				table{
					tr{
						td{
							min-width: 260/50rem;
						}
						td:nth-child(1),td:nth-child(4){
							min-width: 160/50rem;
						}
					}
				}
				.iconfont{
					font-size: 50/50rem;
				}
			}
		}
	}
</style>


